@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

* {
  font-family: "Roboto", sans-serif;
}
.Link {
  color: rgb(107, 107, 107);
  transition: all 0.5s;
}
.Link:hover {
  text-decoration: none;
}

/* Header */
.Header {
  background-color: white;
  border-bottom: 1px solid rgb(230, 230, 230);
}
.Header .Link {
  font-weight: 600;
  font-size: 20px;
  transition: all 0.5s;
  margin: 0 10px;
}
.Header .Toggler {
  border: none;
  transition: all 0.4s;
}
.Header .Toggler:focus {
  outline: none;
}
.Header .NavLink {
  border-top: 1px solid #F8F9fA;
  border-bottom: 1px solid #F8F9fA;
  transition: all 0.5s;
  padding: 10px 0;
}
.Header .NavLink:hover {
  background-color: white;
  border-top-color: rgb(0, 248, 0);
  border-bottom-color: rgb(0, 248, 0);
}
.Header .Link:hover {
  color: black;
}
/*Home*/
.Home {
  min-height: 70vh;
}
.Home .question {
  margin: 100px 0 20px 0;
  display: block;
  font-size: 38px;
  font-weight: bold;
}
.Home .answer {
  font-size: 30px;
  font-weight: 500;
  color: rgb(0, 220, 55);
}
.Home .select {
  margin-left: -10px;
  margin-right: 10px;
  width: 80%;
  font-size: 30px;
  border: none;
  border-bottom: 2px solid red;
  color: rgb(0, 220, 55);
  transition: all 1s;
  cursor: pointer;
}
.Home .select:focus {
  outline: none

}
.Home .selected {
  border-bottom: 2px solid rgb(0, 255, 64);
}
.Home .select option {
  font-size: 14px;
  color: black;
}
.Home .select option:checked:hover {
  background: #000 !important;
}
.Home button {
  font-weight: bold;
  background-color: white;
  padding: 10px 50px;
  border-radius: 20px;
  transition: all 0.5s;
  border: 2px solid rgb(0, 255, 64);
}
.Home button:hover {
  background-color: rgb(0, 255, 64);
  color: white;
}
.Home button:disabled:hover {
  background-color: white;
}
.Home button:focus {
  outline: none;
}
.Home button:disabled {
  border: 2px solid grey;
  color: grey;
}
/* Scroll bar */
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: #ffffff; 
}
::-webkit-scrollbar-thumb {
  background: rgb(123, 255, 145); 
  border-radius: 8px;
}
::-webkit-scrollbar-thumb:hover {
  background:  rgb(27, 216, 59); 
}
/* Weather */
.Weather {
  background-color: rgb(246, 255, 250);
}
.Weather .Content {
  border-radius: 10px;
  border: 1px solid rgb(100, 255, 100);
  margin-top: 50px;
  background-color: white;
}
.Weather .Table {
  border-top: 2px solid lime;
  border-bottom: 2px solid lime;
}
select {
  /*for firefox*/
  -moz-appearance: none;
  /*for chrome*/
  -webkit-appearance:none;
  appearance: none;
}
/*for IE10+*/
select::-ms-expand {
    display: none;
}
/*Chart*/
.Chart {
  margin-top: 50px;
  margin-bottom: 20px;
}
/* About */
.About {
  background-color:rgb(246, 255, 250);
  min-height: 1000px;
}
.About .Content {
  padding: 30px 10px;
  text-align: left;
  max-width: 500px;

}
.about-title {
  padding-top: 30px;
  font-size: 20px;
  font-weight: bold;
}
.about-body {
  padding-left: 20px;
}
.about-content {
  padding-left: 20px;
}
/* Footer */
.Footer {
  position: relative;
  top: 50px;
  bottom: 0px;
}
